import React, { Component } from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import { getIncomeList } from '../../actions/transactionsActions';


class Income extends Component {
    state = {
        incomeList: [],
    }

    componentDidMount() {
        this.props.getIncomeList(this.props.auth.user.name);
    }

    componentDidUpdate(prevProps, prevState) {
        if(prevProps.transaction !== this.props.transaction) {
            this.setState({
                incomeList: this.props.transaction.incomeList
            }, () => {
                console.log(this.state.incomeList);
            })
        }
    }

    render() {
        return (
            <IncomeWrapper>
                <div className="income-wrapper">
                    <div className="header-wrapper">
                        <div className="header text-white">Income / Received</div>
                    </div>
                    <table className="text-white">
                        <tbody>
                            {this.state.incomeList[0].source}
                            {/* { this.state.incomeList.map((income, idx) => {
                                return (
                                    <tr key={`income${idx}`}>
                                        <td>{income.source}</td>
                                        <td>${income.amount}</td>
                                    </tr>
                                )
                            }) 
                            } */}
                        </tbody>
                    </table>
                </div>
            </IncomeWrapper>
        )
    }
}

const IncomeWrapper = styled.div`
margin-left: 2rem;
.income-wrapper {
    height: 25rem;
    background-color: #3a4149;
    border: 1px solid #000;
    margin-left: 2rem
}

table {
    width: 95%;
    margin: 0 auto;
}

tr {
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

tr:last-child {
    border-bottom: none
}

td {
    padding: 12px 20px;
    font-size: 20px;
}

.header {
    text-align: left;
    font-family: 'Open Sans';
    padding: 1.3rem 1rem 1rem 1rem;

}
.header-wrapper {
    background-color: #343b41;
    border-bottom: 1px solid #000;
}
`

const mapStateToProps = (state) => ({
    auth: state.auth,
    transaction: state.transactions
});

export default connect(mapStateToProps, { getIncomeList }) (Income);